
* {
  font-family: 'Martian Mono', monospace; 
}

h1 {
  font-family: 'Martian Mono', monospace;
}

h2 {
  font-family: 'Montserrat', sans-serif; 
}

#home {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 130px;
}

/* Rounded border */

.intro-title {
  font-size: 35px;
  text-align: center;
  margin-bottom: 10px;
  font-weight: bold;
}

.intro-name {
  font-weight: bold;
  font-size: 35px;
  text-align: center;
}

.intro-subtitle {
  font-weight: 500;
  text-align: center;
  font-size: 15px;
  margin-top: 15px;
  margin-bottom: 20px;
}

.intro-description {
  font-size: 20px;
  max-width: 45%;
  text-align: justify;
  margin-top: 20px;
}


.intro-linkedin,
.intro-contactme,
.intro-github {
  margin-right: 20px;
}





.intro-linkedin:hover {
  content: url('/Users/lixuanguo/webdev/portfolio-2024/public/linkedin_icon_hover.png'); 
  height:25px;
}


.intro-github:hover {
